header {
  height: 100%;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

.cta {
  display: flex;
  justify-content: center;
  gap: 1.2rem;
  margin-top: 2.5rem;
}

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 5rem;
  bottom: 1rem;
}

.header__socials::after {
  content: '';
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

.scroll__down {
  position: absolute;
  right: 5rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}
.text-light {
  display: inline-block;
  font-size: 2rem;
  white-space: nowrap;
  overflow: hidden;
  /* border-right: 0.15em solid orange; */
  border-right: 0.15em solid white;
  animation: typing 1s steps(30, end), blink-caret 0.75s step-end infinite;

  /* Optional styling */
  color: white;
  text-shadow: 2px 2px 0px #000;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    /* border-color: orange; */
    border-color:white;
  }
}

@media screen and (max-width: 1024px){
  header {
    padding-top: 3rem;
  }

  section {
    margin-top: 6rem;
  }
}

@media screen and (max-width: 600px){

  .header__socials,
  .scroll__down {
    display: none;
  }
}


